.board {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 2rem;
  -webkit-transition: all 2s linear;
  -o-transition: all 2s linear;
  transition: all 2s linear; }

.board__left,
.board__right {
  width: 6rem; }

.board__center {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-pack: justify;
      justify-content: space-between; }

.board__icon-rival,
.board__icon-user {
  -ms-flex: 1 1;
      flex: 1 1;
  height: 7rem;
  margin: 0 1rem;
  -ms-flex-item-align: center;
      align-self: center;
  background-position: 50% 50%;
  background-size: contain;
  background-repeat: no-repeat;
  opacity: 1;
  -webkit-transition: opacity 400ms ease-out;
  -o-transition: opacity 400ms ease-out;
  transition: opacity 400ms ease-out; }

.board__left.loss {
  color: #C7040A; }

.board__right.win {
  color: #C7040A; }

.board__icon-rival {
  background-image: url("./finger-upp-rival.svg"); }

.board__icon-user {
  background-image: url("./scissors_user_upp.svg"); }

.board__icon-rival.paper {
  background-image: url("./paper_rival.svg"); }

.board__icon-user.paper {
  background-image: url("./paper_user.svg"); }

.board__icon-rival.scissors {
  background-image: url("./scissors_rival.svg"); }

.board__icon-user.scissors {
  background-image: url("./scissors_user.svg"); }

.board__icon-rival.rock {
  background-image: url("./rock_rival.svg"); }

.board__icon-user.rock {
  background-image: url("./rock_user.svg"); }

.board__icon-rival.paper.loss {
  background-image: url("./paper_rival_red.svg"); }

.board__icon-user.paper.win {
  background-image: url("./paper_user_red.svg"); }

.board__icon-rival.scissors.loss {
  background-image: url("./scissors_rival_red.svg"); }

.board__icon-user.scissors.win {
  background-image: url("./scissors_user_red.svg"); }

.board__icon-rival.rock.loss {
  background-image: url("./rock_rival_red.svg"); }

.board__icon-user.rock.win {
  background-image: url("./rock_user_red.svg"); }

._anim .board__icon-rival,
._anim .board__icon-user {
  opacity: .7; }



